export interface ButtonStates {
  text: string;
  border: string;
}

type OutlinedButtonStates = Pick<ButtonStates, "border" | "text">;

export interface ButtonKinds {
  activeBackground: string;
  background: string;
  focus: string;
  hoverBackground: string;
  hoverText: string;
  loadingLeft: string;
  loadingMiddle: string;
  loadingRight: string;
  contained: ButtonStates;
  outlined: OutlinedButtonStates;
  text: OutlinedButtonStates;
}
