import React from "react";
import { ButtonOverrides, SIZE } from "baseui/button";
import { StyleObject } from "styletron-react";
import { Theme } from "@/src/baseWeb";
import { StyledLoader } from "./buttonLoader";

type ButtonVariants =
  | "brand"
  | "positive"
  | "negative"
  | "neutral"
  | "warning"
  | "decorative";

const KIND = {
  contained: "contained",
  outlined: "outlined",
  text: "text",
};

interface StyleProps {
  $disabled: boolean;
  $size: SIZE[keyof SIZE];
  variant: ButtonVariants;
}

const buttonVariants = {
  brand: "brand",
  positive: "positive",
  negative: "negative",
  neutral: "neutral",
  warning: "warning",
  decorative: "decorative",
};

const buttonSizes = ($theme: Theme) => {
  const {
    scale300,
    scale500,
    scale550,
    scale600,
    scale650,
    scale800,
    scale900,
    scale1000,
    scale1200,
    scale1400,
  } = $theme.sizing;
  return {
    mini: {
      fontSize: scale500,
      height: scale900,
      padding: `0 ${scale300}`,
    },
    compact: {
      fontSize: scale550,
      height: scale1000,
      padding: `0 ${scale600}`,
    },
    default: {
      fontSize: scale600,
      height: scale1200,
      padding: `0 ${scale800}`,
    },
    large: {
      fontSize: scale650,
      height: scale1400,
      padding: `0 ${scale900}`,
    },
  };
};

const buttonOverrides = (
  $theme: Theme,
  block = false,
  kind = KIND.contained,
  variant = buttonVariants.brand
): ButtonOverrides => {
  const isContained = kind === KIND.contained;
  const { colors, animation, sizing } = $theme;
  const {
    disabledBackground,
    disabledBackgroundContained,
    disabledBorder,
    disabledTextContained,
    disabledText,
    ...rest
  } = colors.button;
  const { scale100, scale300 } = sizing;

  const {
    activeBackground,
    background,
    focus,
    hoverBackground,
    hoverText,
    loadingLeft,
    loadingMiddle,
    loadingRight,
    ...buttonVariant
  } = rest[variant || buttonVariants.brand];

  const { border, text } = buttonVariant[kind || KIND.contained];

  return {
    BaseButton: {
      style: ({ $disabled, $size }: StyleProps): StyleObject => ({
        ...$theme.borders.borderWidth100,
        color: text,
        backgroundColor: isContained ? background : "transparent",
        borderColor: kind === KIND.text ? "transparent" : border,
        fontFamily: "Public Sans",
        fontWeight: 600,
        transitionDuration: `${animation.timing200}`,
        transitionTimingFunction: "ease, ease",
        width: block ? "100%" : "auto",
        ...buttonSizes($theme)?.[$size],
        ":active": {
          ...($disabled
            ? {}
            : {
                backgroundColor: activeBackground,
                color: hoverText,
              }),
        },
        ...($disabled
          ? {}
          : {
              ":hover": {
                backgroundColor: hoverBackground,
                borderColor: hoverBackground,
                color: hoverText,
              },
              ":focus": {
                boxShadow: "none",
                outline: "solid",
                borderColor: focus,
                outlineColor: focus,
                outlineWidth: scale100,
              },
            }),
        ":disabled": {
          backgroundColor: isContained
            ? disabledBackgroundContained
            : disabledBackground,
          borderColor: disabledBorder[kind],
          color: isContained ? disabledTextContained : disabledText,
        },
      }),
    },
    StartEnhancer: {
      style: ({ $size }: StyleProps): StyleObject => ({
        marginRight:
          $size === "compact" || $size === "mini" ? scale100 : scale300,
      }),
    },
    EndEnhancer: {
      style: ({ $size }: StyleProps): StyleObject => ({
        marginLeft:
          $size === "compact" || $size === "mini" ? scale100 : scale300,
      }),
    },
    LoadingSpinner: {
      component: () => (
        <StyledLoader
          leftColor={loadingLeft}
          middleColor={loadingMiddle}
          rightColor={loadingRight}
        />
      ),
    },
  };
};

export { ButtonVariants, buttonOverrides };
